import React, { useEffect, useRef } from 'react';
import Recommendation from './Recommendation';
import styles from './Recommendations.module.css';
import './Recommendations.css';
import { createStructuredSelector } from 'reselect';
import { connect } from 'react-redux';
import { shownRecsSelector } from '../../../redux/recommendations/selectors';
import {
  recActionSubmit,
  updateShownRecs,
} from '../../../redux/recommendations/actions';
import { CSSTransition, TransitionGroup } from 'react-transition-group';

const { ipcRenderer } = require('electron');

const Recommendations = ({ shownRecs, addRec, submitRecAction }) => {
  const recs = useRef(shownRecs);
  recs.current = shownRecs;

  useEffect(() => {
    ipcRenderer?.on('newRecProps', (event, props) => {
      addRec(props);
    });
  }, [addRec]);

  return (
    <div className={styles.Root}>
      <TransitionGroup>
        {shownRecs.slice(0, 3).map((item) => (
          <CSSTransition key={item.id} timeout={300} classNames="rec-animation">
            <Recommendation
              key={item.id}
              id={item.id}
              text={item.text}
              observedTarget={item.observed_tag}
              units={item.units || " ~"}
              precision={item.precision}
              diffCond={item.diff}
              workspaceId={item.workspaceId || "M100"}
              handleClose={submitRecAction}
            />
          </CSSTransition>
        ))}
      </TransitionGroup>
    </div>
  );
};

const mapStateToProps = createStructuredSelector({
  shownRecs: shownRecsSelector,
});

const mapDispatchToProps = (dispatch) => ({
  addRec: (newRecs) => dispatch(updateShownRecs(newRecs)),
  submitRecAction: (action, recId) => dispatch(recActionSubmit(action, recId)),
});

export default connect(mapStateToProps, mapDispatchToProps)(Recommendations);
