import React from 'react';

import styles from './TimeGroup.module.css';
import { FormControl, FormControlLabel, RadioGroup } from '@material-ui/core';
import WhiteRadio from '../RadioButtons';
import cn from 'classnames';

const TimeGroup = ({ timePeriod, handleTimePeriodChange }) => {
  const handleChange = (ev) => {
    const value = ev.target.value;
    handleTimePeriodChange(value);
  };

  return (
    <div className={styles.Root}>
      <FormControl component="fieldset">
        <h1 className={styles.FormLabel}>Time period</h1>
        <RadioGroup
          aria-label="DownloadPeriod"
          name="DownloadPeriod"
          value={timePeriod}
          onChange={handleChange}
        >
          <FormControlLabel
            classes={{
              label: cn(styles.RadioLabel, {
                [styles.RadioLabelActive]: timePeriod === '1',
              }),
            }}
            value="1"
            control={<WhiteRadio />}
            label="Day"
          />
          <FormControlLabel
            classes={{
              label: cn(styles.RadioLabel, {
                [styles.RadioLabelActive]: timePeriod === '7',
              }),
            }}
            value="7"
            control={<WhiteRadio />}
            label="Week"
          />
          <FormControlLabel
            classes={{
              label: cn(styles.RadioLabel, {
                [styles.RadioLabelActive]: timePeriod === '30',
              }),
            }}
            value="30"
            control={<WhiteRadio />}
            label="Month"
          />
        </RadioGroup>
      </FormControl>
    </div>
  );
};

export default TimeGroup;
