import { createSelector } from 'reselect';

const streamsSelector = (state) => state.plotsStreams;

export const plotsStreamsByWorkspaceId = createSelector(
  streamsSelector,
  (_, { workspaceId }) => workspaceId,
  (streams, id) => streams[id]
);

export const streamTargetsSelector = createSelector(
  plotsStreamsByWorkspaceId,
  (_, { streamId }) => streamId,
  (streams, id) => streams[id].targets
);

export const streamDefaultTargetsSelector = createSelector(
  plotsStreamsByWorkspaceId,
  (_, { streamId }) => streamId,
  (streams, id) => streams[id].defaultTargets
);

export const streamTextSelector = createSelector(
  plotsStreamsByWorkspaceId,
  (_, { streamId }) => streamId,
  (streams, id) => streams[id].text
);
