import produce from 'immer';
import {
  CLOSE_SPEC_DIALOG,
  FAILURE,
  OPEN_SPEC_DIALOG,
  SAVE_SPECIFICATIONS,
  SET_SPECIFICATIONS,
  SUCCESS,
  SET_REGIME,
  UPDATE_SPECIFICATIONS,
} from '../constants';

const initialState = {
  isOpened: false,
  workspaceId: null,
  specs: {
    '3SS': {
      entities: {},
      error: null,
    },
    M100: {
      entities: {},
      error: null,
    },
  },
};

const reducer = (state = initialState, action) =>
  produce(state, (draft) => {
    const { type, payload, response, error } = action;

    switch (type) {
      case OPEN_SPEC_DIALOG + SUCCESS:
        const regime = Object.keys(response)[0];
        const specifications = response[regime];

        draft.isOpened = true;
        draft.workspaceId = payload.id;
        draft.specs[payload.id].entities.regime = regime;
        draft.specs[payload.id].entities.consts = specifications;
        draft.specs[payload.id].error = null;
        break;
      case OPEN_SPEC_DIALOG + FAILURE:
        draft.isOpened = true;
        draft.workspaceId = payload.id;
        draft.specs[payload.id].entities.regime = 'conv';
        draft.specs[payload.id].entities.consts = {
          conv: 35.7,
          select: 2.34,
          S_AP: 95.5,
          S_BP: 1.5,
          B_APC: 1.5,
        };
        draft.specs[payload.id].error = error;
        break;
      case SET_SPECIFICATIONS:
        draft.specs[draft.workspaceId].entities.consts = payload.specs;
        break;
      case UPDATE_SPECIFICATIONS + SUCCESS:
        draft.specs[draft.workspaceId].entities.consts = response;
        break;
      case UPDATE_SPECIFICATIONS + FAILURE:
        draft.specs[draft.workspaceId].entities.consts = {
          conv: 35.7,
          select: 2.34,
          S_AP: 95.5,
          S_BP: 1.5,
          B_APC: 1.5,
        };
        break;
      case SET_REGIME:
        draft.specs[draft.workspaceId].entities.regime = payload.regime;
        break;
      case CLOSE_SPEC_DIALOG:
        draft.isOpened = false;
        draft.workspaceId = null;
        break;
      case SAVE_SPECIFICATIONS + SUCCESS:
        draft.isOpened = false;
        draft.workspaceId = null;
        break;
      case SAVE_SPECIFICATIONS + FAILURE:
        draft.isOpened = false;
        draft.workspaceId = null;
        break;
      default:
        return;
    }
  });

export default reducer;
