import { createSelector } from 'reselect';

const appPropertiesSelector = (state) => state.appProperties;

export const currentWorkspace = createSelector(
  appPropertiesSelector,
  (appProps) => {
    return appProps.current;
  }
);

export const allWorkspaces = createSelector(
  appPropertiesSelector,
  (appProps) => {
    return appProps.workspaces;
  }
);

export const updateInterval = createSelector(
  appPropertiesSelector,
  (appProps) => {
    return appProps.updateInterval;
  }
);
