import React from "react";
import cn from "classnames";

import DownloadIcon from "../../../../../assets/images/WidgetSettings_Download.png";
import SpecificationIcon from "../../../../../assets/images/WidgetSettings_Specification.png";

import styles from "./SettingsBar.module.css";
import { connect, useSelector } from "react-redux";
import { createStructuredSelector } from "reselect";
import { currentWorkspace } from "../../../../../redux/appProperties/selectors";
import { setAppWorkspace } from "../../../../../redux/appProperties/actions";
import { openDownloadDialog } from "../../../../../redux/downloadDialog/actions";
import { openSpecDialog } from "../../../../../redux/specDialog/actions";
import { getLastPoint } from "../../../../Workspace/Advices/Advice/utils";

const SettingsBar = ({ name, currentWorkspace, setWorkspace, openDownload, openSpecs }) => {
  const checked = name === currentWorkspace;
  const dataset = useSelector((state) => state.datasets[name]?.entities);
  let procMarker = getLastPoint(dataset, "A30_RC", null);
  procMarker = ["object", "undefined", "number", "boolean"].includes(typeof procMarker)
    ? " ~ "
    : procMarker.toUpperCase();

  return (
    <div className={styles.Root}>
      <button onClick={setWorkspace} className={cn(styles.Name, { [styles.Checked]: checked })}>
        {name === "3SS" ? procMarker : name.toUpperCase()}
      </button>
      <button className={styles.IconButton} onClick={openSpecs}>
        <img src={SpecificationIcon} alt={""} />
      </button>
      <button className={styles.IconButton} onClick={openDownload}>
        <img src={DownloadIcon} alt={""} />
      </button>
    </div>
  );
};

const mapStateToProps = createStructuredSelector({
  currentWorkspace: currentWorkspace,
});

const mapDispatchToProps = (dispatch, ownProps) => ({
  setWorkspace: () => dispatch(setAppWorkspace(ownProps.name)),
  openDownload: () => dispatch(openDownloadDialog(ownProps.name)),
  openSpecs: () => dispatch(openSpecDialog(ownProps.name)),
});

export default connect(mapStateToProps, mapDispatchToProps)(SettingsBar);
