import React from 'react';
import styles from './SpecInput.module.css';
import cn from 'classnames';
import './SpecInput.css';
import { setSpecifications } from '../../../../redux/specDialog/actions';
import { connect } from 'react-redux';

const SpecInput = ({ spec, text, currSpecs, setSpecs, tagId }) => {
  const updateSpec = (ev) => {
    const value = ev.target.value;
    if (value.length > 4) return;

    setSpecs({
      ...currSpecs,
      [tagId]: value,
    });
  };

  return (
    <div className={styles.Root}>
      <h1 className={styles.Text}>{text}</h1>
      <input
        className={cn({[styles.Invalid]: !spec || Number(spec) > 100})}
        id={`${spec}-spec-input`}
        type={'number'}
        step="0.01"
        value={spec}
        onChange={updateSpec}
      />
      <label
        className={cn({[styles.InvalidLabel]: !spec || Number(spec) > 100})}
        htmlFor={`${spec}-spec-input`}>%</label>
    </div>
  );
};

const mapDispatchToProps = (dispatch) => ({
  setSpecs: (specs) => dispatch(setSpecifications(specs)),
});

export default connect(null, mapDispatchToProps)(SpecInput);
