import React from "react";
import Row from "./Row/Row";
import styles from "./InfoCell.module.css";
import { createStructuredSelector } from "reselect";
import { datasetByWorkspaceIdSelector } from "../../../../../../redux/datasets/selectors";
import { connect } from "react-redux";
import { getLastPoint } from "../../../../../Workspace/Advices/Advice/utils";
import cn from "classnames";

const InfoCell = ({ props, workspaceId, dataset }) => {
  const exclTags = ["S_BP", "B_APC", "Δ T"];
  let procMarker = getLastPoint(dataset, "A30_RC", null);
  procMarker = ["object", "undefined", "number", "boolean"].includes(typeof procMarker)
    ? " ~ "
    : procMarker.toUpperCase();
  const isHidden = workspaceId === "3SS" && exclTags.includes(props.name) && procMarker !== "3SS";

  return (
    <div className={cn(styles.Root, { [styles.Hidden]: isHidden })}>
      <span className={styles.Name}>{props.name}</span>
      <div className={styles.Info}>
        {props.rows.map((rowProps) => (
          <Row
            key={rowProps.name + rowProps.units}
            name={rowProps.name}
            targetId={rowProps.targetId}
            units={rowProps.units}
            precision={rowProps.precision}
            isSensor={rowProps.sensor}
            texted={rowProps.texted}
            workspaceId={workspaceId}
          />
        ))}
      </div>
    </div>
  );
};

const mapStateToProps = createStructuredSelector({
  dataset: datasetByWorkspaceIdSelector,
});

export default connect(mapStateToProps)(InfoCell);
