import produce from 'immer';

import { FAILURE, LOAD_SENSORS_DATA, REQUEST, SUCCESS } from '../constants';

const initialState = {
  '3SS': {
    loading: false,
    loaded: true,
    error: null,
    entities: {},
  },
  M100: {
    loading: false,
    loaded: true,
    error: null,
    entities: {},
  },
};

const reducer = (state = initialState, action) =>
  produce(state, (draft) => {
    const { type, payload, response, error } = action;

    switch (type) {
      case LOAD_SENSORS_DATA + REQUEST:
        draft[payload.id].loading = true;
        draft[payload.id].loaded = false;
        draft[payload.id].error = null;
        break;
      case LOAD_SENSORS_DATA + SUCCESS:
        draft[payload.id].loading = false;
        draft[payload.id].loaded = true;
        draft[payload.id].error = null;
        draft[payload.id].entities = response;
        break;
      case LOAD_SENSORS_DATA + FAILURE:
        draft[payload.id].loading = false;
        draft[payload.id].loaded = false;
        draft[payload.id].error = error;
        break;
      default:
        return;
    }
  });

export default reducer;
