import React, { useEffect, useState } from 'react';
import styles from './Recommendation.module.css';
import CrossImg from '../../../../assets/images/Rec_Cross.png';
import { countToTime, prepareValue } from './utils';
import { connect } from 'react-redux';
import { createStructuredSelector } from 'reselect';
import { datasetByWorkspaceIdSelector } from '../../../../redux/datasets/selectors';
import { sensorsByWorkspaceIdSelector } from '../../../../redux/sensorsData/selectors';

// TODO: Fix double rerender
const Recommendation = ({
  text,
  handleClose,
  id,
  observedTarget,
  precision,
  units,
  diffCond,
  dataset,
  sensors,
}) => {
  let observedValue;
  let observedSensor;
  const [count, setCount] = useState(0);

  if (observedTarget && Object.keys(dataset).length) {
    try {
      observedValue = prepareValue(dataset, observedTarget, precision, 'opt');

      if (diffCond) {
        observedSensor = prepareValue(sensors, observedTarget, precision, '');
        const diff = Math.abs(observedValue - observedSensor);
        if (diff < diffCond) handleClose('DECLINED', id);
      }
    } catch (err) {
      observedValue = 'None';
    }
  } else {
    observedValue = '';
  }

  useEffect(() => {
    const timerUpdater = setTimeout(() => setCount(count + 1), 60000);
    return () => clearTimeout(timerUpdater);
  }, [count, setCount]);

  let resText = text + observedValue
  if (observedValue) {
    resText += units
  }

  return (
    <div className={styles.Root}>
      <span className={styles.Timer}>{countToTime(count)}</span>
      <button
        className={styles.CloseButton}
        onClick={() => handleClose('DECLINED', id)}
      >
        <img src={CrossImg} alt="Cross" />
      </button>
      <h1 className={styles.Text}>{resText}</h1>
    </div>
  );
};

const mapStateToProps = createStructuredSelector({
  dataset: datasetByWorkspaceIdSelector,
  sensors: sensorsByWorkspaceIdSelector,
});

export default connect(mapStateToProps)(Recommendation);
