import produce from 'immer';
import {
  SET_PLOT_STREAM,
  SET_PLOT_PERIOD,
  SET_PLOT_TARGETS,
} from '../constants';

const initialState = {
  '3SS': {
    left: {
      streamId: 'main',
      targets: ['S_BP', 'S_BP_lab', 'B_APC', 'B_APC_lab'],
      period: 1440,
    },
    right: {
      streamId: 'process',
      targets: ['B3FT33_opt', 'B3TE32', 'B3FT31', ''],
      period: 1440,
    },
  },
  M100: {
    left: {
      streamId: 'main',
      targets: ['FT101E', 'FT101E_opt', 'S_CONV_ai', ''],
      period: 1440,
    },
    right: {
      streamId: 'cond',
      targets: ['F102AB', 'F102CD', 'F102EF', 'MF102'],
      period: 1440,
    },
  },
};

const reducer = (state = initialState, action) =>
  produce(state, (draft) => {
    const { type, payload } = action;

    switch (type) {
      case SET_PLOT_STREAM:
        draft[payload.workspaceId][payload.plotId].streamId = payload.streamId;
        draft[payload.workspaceId][payload.plotId].targets =
          payload.defaultTargets;
        break;
      case SET_PLOT_PERIOD:
        draft[payload.workspaceId][payload.plotId].period = payload.period;
        break;
      case SET_PLOT_TARGETS:
        if (payload.targets.length >= 5) {
          break;
        } else {
          draft[payload.workspaceId][payload.plotId].targets = payload.targets;
          break;
        }
      default:
        return;
    }
  });

export default reducer;
