import { createSelector } from 'reselect';

const specDialogProps = (state) => state.specDialog;

export const isSpecDialogOpened = createSelector(
  specDialogProps,
  (props) => props.isOpened
);

export const specPropsSelector = createSelector(
  specDialogProps,
  (props) => props.specs
);

export const specWorkspaceIdSelector = createSelector(
  specDialogProps,
  (props) => props.workspaceId
);
