import React from 'react';
import cn from 'classnames';

import SchemeM100_3 from '../../../assets/images/Scheme_M100.png';
import SchemeM100_2 from '../../../assets/images/Scheme_M100(mini).png';
import Scheme3SS from '../../../assets/images/Scheme_3SS.png';

import styles from './Scheme.module.css';
import './Scheme.css';
import { createStructuredSelector } from 'reselect';
import { currentWorkspace } from '../../../redux/appProperties/selectors';
import { connect, useSelector } from 'react-redux';
import { CSSTransition, SwitchTransition } from 'react-transition-group';

const schemes = {
  '3SS': Scheme3SS,
  M100: SchemeM100_3,
};

const schemePosStyle = {
  '3SS': 'ss',
  M100: 'M100',
};

const Scheme = ({ workspaceId }) => {
  const data = useSelector(
    (state) => state.datasets[workspaceId].entities['actual_scheme']
  );

  if (workspaceId === 'M100' && data) {
    const scheme_marker = Number(Object.values(data).slice(-1)[0]);

    if (scheme_marker === 3) {
      schemes[workspaceId] = SchemeM100_3;
    } else {
      schemes[workspaceId] = SchemeM100_2;
    }
  }

  return (
    <SwitchTransition>
      <CSSTransition
        key={workspaceId}
        addEndListener={(node, done) =>
          node.addEventListener('transitionend', done, false)
        }
        classNames="scheme-fade"
      >
        <img
          src={schemes[workspaceId]}
          alt={''}
          className={cn(styles.Root, {
            [styles[schemePosStyle[workspaceId]]]: workspaceId,
          })}
        />
      </CSSTransition>
    </SwitchTransition>
  );
};

const mapStateToProps = createStructuredSelector({
  workspaceId: currentWorkspace,
});

export default connect(mapStateToProps)(Scheme);
