import { FAILURE, REQUEST, SUCCESS } from '../constants';

const API_ENDPOINT = 'http://45.61.48.18/';
const __DEV__ = process.env.NODE_ENV !== 'production';

const createPostParams = (data) => ({
  method: 'POST',
  headers: { 'Content-Type': 'application/json' },
  body: JSON.stringify(data),
});

const api = (store) => (next) => async (action) => {
  if (!action.CallAPI) return next(action);

  const { CallAPI, type, postData, ...rest } = action;

  next({ ...rest, type: type + REQUEST });
  try {
    const params = postData ? createPostParams(postData) : {};

    const api = __DEV__ ? CallAPI : API_ENDPOINT + CallAPI;

    const response = await fetch(api, params).then(async (res) => {
      if (!res.ok)
        window?.log?.error(`[${res.status}] ${res.statusText} for: ${CallAPI}`);
      const data = await res.json();
      if (res.ok) return data;
      throw data;
    });
    return next({ ...rest, type: type + SUCCESS, response });
  } catch (error) {
    throw next({ ...rest, type: type + FAILURE, error });
  }
};

export default api;
