import { Radio, withStyles } from '@material-ui/core';

const WhiteRadio = withStyles({
  root: {
    color: 'gray',
    '& svg': {
      fontSize: '48px',
    },
  },
  checked: {
    color: 'white',
  },
})((props) => <Radio color="default" {...props} />);

export default WhiteRadio;
