import React from "react";
import cn from "classnames";

import styles from "./Advice.module.css";
import { createStructuredSelector } from "reselect";
import { datasetByWorkspaceIdSelector } from "../../../../redux/datasets/selectors";
import { connect } from "react-redux";
import { getLastPoint } from "./utils";
import { sensorsByWorkspaceIdSelector } from "../../../../redux/sensorsData/selectors";

const Advice = ({
  margin,
  name,
  units,
  optTargetId,
  realTargetId,
  precision,
  dataset,
  sensors,
  workspaceId,
}) => {
  let procMarker = getLastPoint(dataset, "A30_RC", null);
  procMarker = ["object", "undefined", "number", "boolean"].includes(typeof procMarker)
    ? " ~ "
    : procMarker.toUpperCase();
  const isHidden = workspaceId === "3SS" && procMarker !== "3SS";

  return (
    <div className={styles.Root} style={{ left: `${margin}px` }}>
      <span className={styles.Name}>{name}</span>
      <span className={cn(styles.OptVal, { [styles.Hidden]: isHidden })}>
        <span className={styles.OptMarker}>OPT</span>
        <span>{getLastPoint(dataset, optTargetId, precision)}</span>
        <span className={styles.units}>{` ${units}`}</span>
      </span>
      <span className={styles.RealVal}>
        <span className={styles.RealMarker}>REAL</span>
        <span>{getLastPoint(sensors, realTargetId, precision)}</span>
        <span className={styles.units}>{` ${units}`}</span>
      </span>
    </div>
  );
};

const mapStateToProps = createStructuredSelector({
  dataset: datasetByWorkspaceIdSelector,
  sensors: sensorsByWorkspaceIdSelector,
});

export default connect(mapStateToProps)(Advice);
