import smooth from 'array-smooth';

export function interpolateArray(data, fitCount) {
  let linearInterpolate = function (before, after, atPoint) {
    return before + (after - before) * atPoint;
  };

  let newData = [];
  let springFactor = Number((data.length - 1) / (fitCount - 1));
  newData[0] = data[0];
  for (let i = 1; i < fitCount - 1; i++) {
    let tmp = i * springFactor;
    let before = Number(Math.floor(tmp)).toFixed();
    let after = Number(Math.ceil(tmp)).toFixed();
    let atPoint = tmp - before;
    newData[i] = linearInterpolate(data[before], data[after], atPoint);
  }
  newData[fitCount - 1] = data[data.length - 1];
  return newData;
}

export function prepareXAxis(dataset, period = 'max') {
  try {
    const xAxis = Object.values(dataset['time']);
    return typeof period === 'number' ? xAxis.slice(-period) : xAxis;
  } catch (err) {
    return [];
  }
}

export function prepareTraceData(
  dataset,
  target,
  period = 'max',
  interpolate = false,
  smoothing = true
) {
  try {
    if (!target.length) return [];

    let result = Object.values(dataset[target]);
    if (typeof period === 'number') result = result.slice(-period);

    result = result.filter(function (el) {
      return el != null;
    });

    // if (interpolate && typeof period === 'number')
    //   result = interpolateArray(result.slice(-period), period);
    if (smoothing && !target.endsWith('_lab') && !target.endsWith('_setpoint'))
      result = smooth(result, 5);

    return result;
  } catch (err) {
    return [];
  }
}

export function getLastPointIndex(dataset, period = 'all') {
  try {
    if (period === 'all') {
      return Object.values(dataset.time).length - 1;
    } else {
      return Object.values(dataset.time).slice(-period).length - 1;
    }
  } catch (err) {
    return -1;
  }
}

export function checkPlotError(data, target, threshold) {
  try {
    const lastValue = Object.values(data[target]).slice(-1)[0];
    if (Object.values(data[target]).length < threshold) {
      return [true, 'Not Enough Data'];
    }
    return [!lastValue, !lastValue ? 'No Data' : ''];
  } catch (err) {
    return [true, 'No Data'];
  }
}
