import produce from 'immer';
import {
  CLOSE_DOWNLOAD_DIALOG,
  DOWNLOAD_DATA,
  FAILURE,
  OPEN_DOWNLOAD_DIALOG,
  SET_DOWNLOAD_PERIOD,
  SET_DOWNLOAD_TYPE,
  SUCCESS,
} from '../constants';

const initialState = {
  isOpened: false,
  workspaceId: null,
  dataType: 'params',
  timePeriod: '1',
  error: null,
};

const reducer = (state = initialState, action) =>
  produce(state, (draft) => {
    const { type, payload, error } = action;

    switch (type) {
      case OPEN_DOWNLOAD_DIALOG:
        draft.isOpened = true;
        draft.workspaceId = payload.workspaceId;
        break;
      case CLOSE_DOWNLOAD_DIALOG:
        draft.isOpened = false;
        draft.workspaceId = null;
        break;
      case SET_DOWNLOAD_PERIOD:
        draft.timePeriod = payload.timePeriod;
        break;
      case SET_DOWNLOAD_TYPE:
        draft.dataType = payload.dataType;
        break;
      case DOWNLOAD_DATA + SUCCESS:
        draft.error = null;
        break;
      case DOWNLOAD_DATA + FAILURE:
        draft.error = error;
        break;
      default:
        return;
    }
  });

export default reducer;
