import React from 'react';

import styles from './Row.module.css';
import { prepareMargin, prepareValue } from './utils';
import { createStructuredSelector } from 'reselect';
import { connect } from 'react-redux';
import { datasetByWorkspaceIdSelector } from '../../../../../../../redux/datasets/selectors';
import { sensorsByWorkspaceIdSelector } from '../../../../../../../redux/sensorsData/selectors';

const Row = ({
  name,
  targetId,
  units,
  precision,
  texted,
  dataset,
  sensorsData,
  isSensor,
}) => {
  const rowName = name || '';

  return (
    <div className={styles.Root}>
      <span style={{ marginRight: `${prepareMargin(rowName)}px` }}>
        {rowName}
      </span>
      <span className={styles.Value}>
        {!texted &&
          prepareValue(isSensor ? sensorsData : dataset, targetId, precision)}
      </span>
      <span className={styles.Units}>{units ? ` ${units}` : ''}</span>
    </div>
  );
};

const mapStateToProps = createStructuredSelector({
  dataset: datasetByWorkspaceIdSelector,
  sensorsData: sensorsByWorkspaceIdSelector,
});

export default connect(mapStateToProps)(Row);
