export function getLastPoint(dataset, targetId, precision) {
  try {
    if (precision == null) {
      return Object.values(dataset[targetId]).slice(-1)[0];
    }

    const value = Number(Object.values(dataset[targetId]).slice(-1)[0]);

    if (value <= 0) return Number(0).toFixed(0);

    const result = `${value.toFixed(precision)}`;

    return result.length >= 7 ? result.slice(0, 6) : result;
  } catch (err) {
    return ' ~ ';
  }
}
