import { combineReducers } from 'redux';
import appPropertiesReducer from './appProperties/reducer';
import datasetsReducer from './datasets/reducer';
import sensorsDataReducer from './sensorsData/reducer';
import plotsPropertiesReducer from './plotsProperties/reducer';
import plotsStreamsReducer from './plotsStreams/reducer';
import plotsTargetsReducer from './plotsTargets/reducer';
import shutdownDialogReducer from './shutdownDialog/reducer';
import advicesReducer from './advices/reducer';
import infoWidgetsReducer from './infoWidgets/reducer';
import downloadDialogReducer from './downloadDialog/reducer';
import specDialogReducer from './specDialog/reducer';
import recommendationsReducer from './recommendations/reducer';

const rootReducer = combineReducers({
  appProperties: appPropertiesReducer,
  datasets: datasetsReducer,
  sensorsData: sensorsDataReducer,
  plotsProperties: plotsPropertiesReducer,
  plotsStreams: plotsStreamsReducer,
  plotsTargets: plotsTargetsReducer,
  shutdownDialog: shutdownDialogReducer,
  downloadDialog: downloadDialogReducer,
  specDialog: specDialogReducer,
  advices: advicesReducer,
  infoWidgets: infoWidgetsReducer,
  recommendations: recommendationsReducer,
});

export default rootReducer;
