import React from 'react';
import cn from 'classnames';

import StreamCheckmark from '../../../../../../../assets/images/Stream_Checkmark.png';

import styles from './Stream.module.css';
import { createStructuredSelector } from 'reselect';
import { plotStreamByIdSelector } from '../../../../../../../redux/plotsProperties/selectors';
import { connect } from 'react-redux';
import { setPlotStream } from '../../../../../../../redux/plotsProperties/actions';

const Stream = ({ streamId, text, selectedStream, setStream, closeDropdown }) => {
  const handleClick = () => {
    closeDropdown()
    setStream(streamId);
  };

  return (
    <li className={styles.Root} onClick={handleClick}>
      <span className={styles.Text}>{text}</span>
      <img
        src={StreamCheckmark}
        alt={''}
        className={cn(styles.Checkmark, {
          [styles.Checked]: streamId === selectedStream,
        })}
      />
    </li>
  );
};

const mapStateToProps = createStructuredSelector({
  selectedStream: plotStreamByIdSelector,
});

const mapDispatchToProps = (dispatch, ownProps) => ({
  setStream: (streamId) =>
    dispatch(setPlotStream(ownProps.plotId, streamId, ownProps.workspaceId)),
});

export default connect(mapStateToProps, mapDispatchToProps)(Stream);
