export const prepareValue = (dataset, targetId, precision) => {
  try {
    const value = Number(Object.values(dataset[targetId]).slice(-1)[0]);

    if (['B3SV33A', 'tag23'].includes(targetId))
      return convertBoolToString(targetId, value);

    if (value <= 0) return Number(0).toFixed(0);

    return value.toFixed(precision);
  } catch (err) {
    return ' ~ ';
  }
};

const convertBoolToString = (targetId, value) => {
  switch (targetId) {
    case 'B3SV33A':
      return value ? 'High' : 'Low';
    case 'tag23':
      return value ? 'On' : 'Off';
    default:
      return ' ~ ';
  }
};

export const prepareMargin = (name) => {
  const nameLength = name.length;
  switch (nameLength) {
    case 2:
      return 36;
    case 3:
      return 13;
    default:
      return 0;
  }
};
