import { createSelector } from 'reselect';

const plotsTargetsSelector = (state) => state.plotsTargets;

export const targetsByWorkspaceId = createSelector(
  plotsTargetsSelector,
  (_, { workspaceId }) => workspaceId,
  (targets, id) => targets[id]
);

export const targetUnitsSelector = createSelector(
  targetsByWorkspaceId,
  (_, { targetId }) => targetId,
  (targets, targetId) => targets[targetId].units
);

export const targetTextSelector = createSelector(
  targetsByWorkspaceId,
  (_, { targetId }) => targetId,
  (targets, targetId) => targets[targetId].text
);

export const targetDescriptionSelector = createSelector(
  targetsByWorkspaceId,
  (_, { targetId }) => targetId,
  (targets, targetId) => targets[targetId].description
);
