import React from 'react';
import cn from 'classnames';

import styles from './DownloadDialog.module.css';
import TimeGroup from './TimeGroup/TimeGroup';
import TypeGroup from './TypeGroup';
import { Divider } from '@material-ui/core';
import { connect } from 'react-redux';
import { createStructuredSelector } from 'reselect';
import { downloadDialogPropsSelector } from '../../../redux/downloadDialog/selectors';
import {
  closeDownloadDialog,
  downloadData,
  setDownloadDialogTime,
  setDownloadDialogType,
} from '../../../redux/downloadDialog/actions';

const DownloadDialog = ({
  dialogProps,
  setPeriod,
  setType,
  closeDialog,
  download,
}) => {
  return (
    <div className={cn(styles.Root, { [styles.Opened]: dialogProps.isOpened })}>
      <div className={styles.Blocker} onClick={closeDialog} />
      <div className={styles.Content}>
        <h1 className={styles.Title}>Download data</h1>
        <div>
          <TimeGroup
            handleTimePeriodChange={setPeriod}
            timePeriod={dialogProps.timePeriod}
            groupName={'downloadTimeButtons'}
          />
          <div className={styles.Divider}>
            <Divider />
          </div>
          <TypeGroup
            handleDataTypeChange={setType}
            dataType={dialogProps.dataType}
            groupName={'downloadTypeButtons'}
          />
        </div>
        <div className={styles.Buttons}>
          <button
            onClick={() => {
              download();
              closeDialog();
            }}
            className={styles.WhiteButton}
          >
            DOWNLOAD
          </button>
        </div>
      </div>
    </div>
  );
};

const mapStateToProps = createStructuredSelector({
  dialogProps: downloadDialogPropsSelector,
});

const mapDispatchToProps = (dispatch) => ({
  setPeriod: (period) => dispatch(setDownloadDialogTime(period)),
  setType: (type) => dispatch(setDownloadDialogType(type)),
  closeDialog: () => dispatch(closeDownloadDialog()),
  download: () => dispatch(downloadData()),
});

export default connect(mapStateToProps, mapDispatchToProps)(DownloadDialog);
