import React from 'react';
import Label from './Label';

const defaultLabels = [
  {
    targetId: 'B3SV33A',
    workspaceId: '3SS',
    capitalize: true,
    pos: {
      x: 640,
      y: 1024,
    },
  },
];

const Labels = () => {
  return (
    <>
      {defaultLabels.map((labelProps) => (
        <Label
          key={labelProps.targetId}
          targetId={labelProps.targetId}
          workspaceId={labelProps.workspaceId}
          pos={labelProps.pos}
          capitalize={labelProps.capitalize}
        />
      ))}
    </>
  );
};

export default Labels;
