import React from 'react';
import cn from 'classnames';
import styles from '../SpecDialog/SpecDialog.module.css';
import { createStructuredSelector } from 'reselect';
import {
  closeSpecDialog,
  saveSpecifications,
  setRegime,
  updateSpecInputs,
} from '../../../redux/specDialog/actions';
import { connect } from 'react-redux';
import {
  isSpecDialogOpened,
  specPropsSelector,
  specWorkspaceIdSelector,
} from '../../../redux/specDialog/selectors';
import SpecInput from './SpecInput';
import RegimeButton from './RegimeButton';
import { FormControl, FormControlLabel, RadioGroup } from '@material-ui/core';
import WhiteRadio from '../DownloadDialog/RadioButtons';
import { loadDataset } from '../../../redux/datasets/actions';

const specsOrder = {
  '3SS': [
    {
      tagId: 'S_AP',
      text: 'Alpha in side stream',
    },
    {
      tagId: 'B_APC',
      text: 'Alpha + Camphene in bottom stream',
    },
    {
      tagId: 'S_BP',
      text: 'Beta in side stream',
    },
  ],
  M100: [
    {
      tagId: 'conv',
      text: 'Conversion',
    },
    {
      tagId: 'select',
      text: 'Selectivity',
    },
  ],
};

const SpecDialog = ({
  workspaceId,
  specs,
  isOpened,
  closeDialog,
  saveSpecs,
  setNewRegime,
  updateSpecs,
  loadDataset,
}) => {
  if (workspaceId == null) {
    return (
      <div className={cn(styles.Root, { [styles.Opened]: isOpened })}>
        <div className={styles.Blocker} onClick={closeDialog} />
      </div>
    );
  }

  let currRegime = specs[workspaceId].entities.regime;
  let currSpecs = specs[workspaceId].entities.consts;

  return (
    <div className={cn(styles.Root, { [styles.Opened]: isOpened })}>
      <div className={styles.Blocker} onClick={closeDialog} />
      <div
        className={cn(styles.Content, {
          [styles.WideContent]: workspaceId === '3SS',
          [styles.SmallContent]: workspaceId === 'M100',
        })}
      >
        <h1 className={styles.Title}>Specifications</h1>
        {workspaceId === '3SS' && (
          <>
            <div className={styles.RegimesContainer}>
              {[
                { text: 'Startup', id: 'startup' },
                { text: 'Product quality', id: 'QualityOptimization' },
                { text: 'Energy efficiency', id: 'EnergyOptimization' },
                { text: 'Production rate', id: 'ProductionRateOptimization' },
              ].map((item) => (
                <RegimeButton
                  key={item.text}
                  text={item.text}
                  regimeId={item.id}
                  isChecked={currRegime === item.id}
                  workspaceId={workspaceId}
                />
              ))}
            </div>
            <div className={styles.SpecsContainer}>
              {currSpecs &&
                Object.keys(currSpecs).length !== 0 &&
                specsOrder[workspaceId].map((specProp) => (
                  <SpecInput
                    key={specProp.tagId}
                    spec={currSpecs[specProp.tagId]}
                    text={specProp.text}
                    workspaceId={workspaceId}
                    currSpecs={currSpecs}
                    tagId={specProp.tagId}
                  />
                ))}
            </div>
          </>
        )}
        {workspaceId === 'M100' && (
          <>
            <div className={styles.RadioContainer}>
              <FormControl component="fieldset">
                <RadioGroup
                  aria-label="RadioSpecInput"
                  name="RadioSpecInput"
                  value={currRegime}
                  onChange={(ev) => {
                    const value = ev.target.value;
                    setNewRegime(value);
                    updateSpecs(workspaceId, value);
                  }}
                >
                  <FormControlLabel
                    classes={{
                      label: cn(styles.RadioLabel, {
                        [styles.RadioLabelActive]: currRegime === 'conv',
                      }),
                    }}
                    style={{
                      marginBottom: '33px',
                    }}
                    value="conv"
                    control={<WhiteRadio />}
                    label="Conversion"
                  />
                  <FormControlLabel
                    classes={{
                      label: cn(styles.RadioLabel, {
                        [styles.RadioLabelActive]: currRegime === 'select',
                      }),
                    }}
                    style={{
                      marginBottom: '33px',
                    }}
                    value="select"
                    control={<WhiteRadio />}
                    label="Selectivity"
                  />
                </RadioGroup>
              </FormControl>
            </div>
            <div className={styles.RadioSpecsContainer}>
              {currSpecs &&
                Object.keys(currSpecs).length !== 0 &&
                specsOrder[workspaceId].map((specProp) => (
                  <SpecInput
                    key={specProp.tagId}
                    spec={currSpecs[specProp.tagId]}
                    text={''}
                    workspaceId={workspaceId}
                    currSpecs={currSpecs}
                    tagId={specProp.tagId}
                  />
                ))}
            </div>
          </>
        )}
        <div className={styles.Buttons}>
          <button
            onClick={() => {
              saveSpecs();
              loadDataset(workspaceId);
            }}
            className={styles.WhiteButton}
          >
            SAVE
          </button>
          <button onClick={closeDialog} className={styles.WhiteButton}>
            CANCEL
          </button>
        </div>
      </div>
    </div>
  );
};

const mapStateToProps = createStructuredSelector({
  isOpened: isSpecDialogOpened,
  specs: specPropsSelector,
  workspaceId: specWorkspaceIdSelector,
});

const mapDispatchToProps = (dispatch) => ({
  closeDialog: () => dispatch(closeSpecDialog()),
  saveSpecs: () => dispatch(saveSpecifications()),
  loadDataset: (id) => dispatch(loadDataset(id)),
  setNewRegime: (regimeId) => dispatch(setRegime(regimeId)),
  updateSpecs: (id, regime) => dispatch(updateSpecInputs(id, regime)),
});

export default connect(mapStateToProps, mapDispatchToProps)(SpecDialog);
