import { createSelector } from 'reselect';

export const arrToMap = (arr) =>
  arr.reduce((acc, item) => ({ ...acc, [item.id]: item }), {});

export const getById = (selector, defaultValue) =>
  createSelector(
    selector,
    (_, props) => props.id,
    (entity, id) => entity[id] || defaultValue
  );

export const downloadDataRequestBuilder = (api, dataType, timePeriod) => {
  const data = !dataType ? '' : `data=${dataType}&`;
  const period = !timePeriod ? '' : `period=${timePeriod}&`;
  return `${api}?${data}${period}`;
};

export const recSubmitActionRequestBuilder = (api, recProps, action) => {
  const params =
    `?rec_name=${recProps.rec_name}` +
    `&state=${action}` +
    `&created_dt=${recProps.created_dt}`;
  // `&show_dt=${recProps.show_dt}`;
  // `&click_dt=${moment().unix()}`;
  return api + params;
};

export const saveSpecificationsRequestBuilder = (api, specProps, id) => {
  let params;

  try {
    switch (id) {
      case '3SS':
        params = {
          specification: specProps['regime'] || 'undefined',
          s_ap_setpoint: specProps['consts']['S_AP'],
          s_bp_setpoint: specProps['consts']['S_BP'],
          b_apc_setpoint: specProps['consts']['B_APC'],
        };
        break;
      case 'M100':
        params = {
          specification: specProps['regime'] || 'undefined',
          conv: specProps['consts']['conv'],
          select: specProps['consts']['select'],
        };
        break;

      default:
        throw new Error(`Unknown process ID: ${id}`);
    }
  } catch (err) {
    params = {};
  }

  return `${api}?${objToURLParams(params)}`;
};

const objToURLParams = (obj) => {
  return Object.entries(obj)
    .map(
      ([key, val]) => `${encodeURIComponent(key)}=${encodeURIComponent(val)}`
    )
    .join('&');
};
