import React from 'react';
import { FormControl, FormControlLabel, RadioGroup } from '@material-ui/core';
import WhiteRadio from '../RadioButtons';
import styles from './TypeGroup.module.css';
import cn from 'classnames';

const TypeGroup = ({ dataType, handleDataTypeChange }) => {
  const handleChange = (ev) => {
    const value = ev.target.value;
    handleDataTypeChange(value);
  };

  return (
    <div className={styles.Root}>
      <FormControl component="fieldset">
        <h1 className={styles.FormLabel}>Data type</h1>
        <RadioGroup
          aria-label="DownloadType"
          name="DownloadType"
          value={dataType}
          onChange={handleChange}
        >
          <FormControlLabel
            classes={{
              label: cn(styles.RadioLabel, {
                [styles.RadioLabelActive]: dataType === 'all_data',
              }),
            }}
            value="all_data"
            control={<WhiteRadio />}
            label="All data"
          />
          <FormControlLabel
            classes={{
              label: cn(styles.RadioLabel, {
                [styles.RadioLabelActive]: dataType === 'params',
              }),
            }}
            value="params"
            control={<WhiteRadio />}
            label="Main parameters"
          />
        </RadioGroup>
      </FormControl>
    </div>
  );
};

export default TypeGroup;
