import React from 'react';
import cn from 'classnames';

import styles from './Label.module.css';
import { connect } from 'react-redux';
import { createStructuredSelector } from 'reselect';
import { datasetByWorkspaceIdSelector } from '../../../../redux/datasets/selectors';
import { prepareValue } from '../../../Navigation/InfoWidgets/InfoWidget/InfoTable/InfoCell/Row/utils';
import { currentWorkspace } from '../../../../redux/appProperties/selectors';
import { CSSTransition, SwitchTransition } from 'react-transition-group';

const Label = ({
  pos,
  targetId,
  dataset,
  workspaceId,
  currWorkspace,
  capitalize,
}) => {
  const isShown = workspaceId === currWorkspace;
  const value = prepareValue(dataset, targetId, null);

  return (
    <SwitchTransition>
      <CSSTransition
        key={currWorkspace}
        addEndListener={(node, done) =>
          node.addEventListener('transitionend', done, false)
        }
        classNames="scheme-fade"
      >
        <div
          className={cn(styles.Root, {
            [styles.Shown]: isShown,
          })}
          style={{ top: `${pos.y}px`, left: `${pos.x}px` }}
        >
          {capitalize ? value.toUpperCase() : value}
        </div>
      </CSSTransition>
    </SwitchTransition>
  );
};

const mapStateToProps = createStructuredSelector({
  dataset: datasetByWorkspaceIdSelector,
  currWorkspace: currentWorkspace,
});

export default connect(mapStateToProps)(Label);
