import {
  DOWNLOAD_DATA,
  CLOSE_DOWNLOAD_DIALOG,
  OPEN_DOWNLOAD_DIALOG,
  SET_DOWNLOAD_TYPE,
  SET_DOWNLOAD_PERIOD,
} from '../constants';
import { downloadDataRequestBuilder } from '../utils';

export const openDownloadDialog = (workspaceId) => ({
  type: OPEN_DOWNLOAD_DIALOG,
  payload: { workspaceId },
});

export const closeDownloadDialog = () => ({
  type: CLOSE_DOWNLOAD_DIALOG,
});

export const setDownloadDialogTime = (timePeriod) => ({
  type: SET_DOWNLOAD_PERIOD,
  payload: { timePeriod },
});

export const setDownloadDialogType = (dataType) => ({
  type: SET_DOWNLOAD_TYPE,
  payload: { dataType },
});

export const downloadData = () => async (dispatch, getState) => {
  const state = getState();
  const { dataType, timePeriod, workspaceId } = state.downloadDialog;
  const request = downloadDataRequestBuilder(
    `${workspaceId}/download`,
    dataType,
    timePeriod
  );

  dispatch({
    type: DOWNLOAD_DATA,
    payload: { request },
    CallAPI: request,
  });
};
