export function countToTime(count) {
  const days = Math.floor(count / 1440);
  const hours = Math.floor(count / 60);
  const minutes = count;
  let result = '';

  const checkPostfix = (count) => {
    return count > 1 ? 's ' : ' ';
  };

  if (days) {
    result += `${days} day`;
    result += checkPostfix(days);
  }
  if (hours) {
    result += `${hours} hour`;
    result += checkPostfix(hours);
  }

  result += `${minutes - hours * 60} min ago`;

  return result;
}

export function prepareValue(df, targetId, precision, type) {
  const tagId = type === 'opt' ? targetId : targetId.replace('_opt', '');
  const rawValue = Object.values(df[tagId]).slice(-1)[0];

  return rawValue.toFixed(precision);
}
