import React from 'react';
import cn from 'classnames';

import styles from './Period.module.css';
import { createStructuredSelector } from 'reselect';
import { connect } from 'react-redux';
import { plotPeriodByIdSelector } from '../../../../../../../redux/plotsProperties/selectors';
import { setPlotPeriod } from '../../../../../../../redux/plotsProperties/actions';

const Period = ({ currentPeriod, buttonProps, setPeriod }) => {
  const handleClick = () => {
    setPeriod(buttonProps.minutes);
  };

  return (
    <div
      className={cn(styles.Root, {
        [styles.active]: currentPeriod === buttonProps.minutes,
      })}
      onClick={handleClick}
    >
      {buttonProps.text || '? HOURS'}
    </div>
  );
};

const mapStateToProps = createStructuredSelector({
  currentPeriod: plotPeriodByIdSelector,
});

const mapDispatchToProprs = (dispatch, ownProps) => ({
  setPeriod: (period) =>
    dispatch(setPlotPeriod(ownProps.plotId, period, ownProps.workspaceId)),
});

export default connect(mapStateToProps, mapDispatchToProprs)(Period);
