import React from 'react';

import styles from './Targets.module.css';
import Target from './Target';
import { connect } from 'react-redux';
import { createStructuredSelector } from 'reselect';
import { plotsStreamsByWorkspaceId } from '../../../../../redux/plotsStreams/selectors';
import { plotStreamByIdSelector } from '../../../../../redux/plotsProperties/selectors';

const Targets = ({ plotId, workspaceId, streamsProps, currentStream }) => {
  const targetsList = streamsProps[currentStream].targets;

  return (
    <div className={styles.Root}>
      {targetsList.map((item) => (
        <Target
          key={item}
          targetId={item}
          plotId={plotId}
          workspaceId={workspaceId}
        />
      ))}
    </div>
  );
};

const mapStateToProps = createStructuredSelector({
  streamsProps: plotsStreamsByWorkspaceId,
  currentStream: plotStreamByIdSelector,
});

export default connect(mapStateToProps)(Targets);
