import produce from 'immer';
import {
  FAILURE,
  CLOSE_REC,
  REC_ACTION_SUBMIT,
  SUCCESS,
  UPDATE_SHOWN_RECS,
} from '../constants';

const initialState = {
  shown: [],
  queue: [],
  error: null,
};

const reducer = (state = initialState, action) =>
  produce(state, (draft) => {
    const { type, payload, error } = action;

    switch (type) {
      case UPDATE_SHOWN_RECS:
        // draft.shown = draft.shown.concat(payload.newRec);
        draft.shown = payload.newRecs;
        draft.shown.sort((a, b) => a.priority - b.priority);
        break;
      case REC_ACTION_SUBMIT + SUCCESS:
        draft.shown = draft.shown.filter((item) => item.id !== payload.id);
        draft.error = null;
        break;
      case REC_ACTION_SUBMIT + FAILURE:
        draft.shown = draft.shown.filter((item) => item.id !== payload.id);
        draft.error = error;
        break;
      case CLOSE_REC:
        draft.shown = draft.shown.filter((item) => item.id !== payload.id);
        break;
      default:
        return;
    }
  });

export default reducer;
