import React from 'react';

import styles from './InfoTable.module.css';
import InfoCell from './InfoCell';
import { createStructuredSelector } from 'reselect';
import { infoTableByWorkspaceId } from '../../../../../redux/infoWidgets/selectors';
import { connect, useSelector } from 'react-redux';

const InfoTable = ({ table, workspaceId }) => {
  let type = 'full';
  let tableByWorkspaceId = table[type];
  const data = useSelector(
    (state) => state.datasets[workspaceId].entities['actual_scheme']
  );

  if (workspaceId === 'M100' && data) {
    const scheme_marker = Number(Object.values(data).slice(-1)[0]);

    if (scheme_marker === 3) {
      type = 'full';
      tableByWorkspaceId = table[type];
    } else {
      type = 'mini';
      tableByWorkspaceId = table[type];
    }
  }

  return (
    <div className={styles.Root}>
      {tableByWorkspaceId.map((cellProps) => (
        <InfoCell
          key={cellProps.name}
          props={cellProps}
          workspaceId={workspaceId}
        />
      ))}
    </div>
  );
};

const mapStateToProps = createStructuredSelector({
  table: infoTableByWorkspaceId,
});

export default connect(mapStateToProps)(InfoTable);
