import React, { useEffect } from 'react';
import { connect } from 'react-redux';

import { loadDataset } from '../../redux/datasets/actions';

import FrameBar from '../FrameBar';
import Navigation from '../Navigation';
import Workspace from '../Workspace';

import styles from './App.module.css';
import { createStructuredSelector } from 'reselect';
import {
  allWorkspaces,
  updateInterval,
} from '../../redux/appProperties/selectors';
import { loadSensorsData } from '../../redux/sensorsData/actions';
import { requestCalculation } from './utils';
import { requestNewRecs } from '../../redux/recommendations/actions';

const { ipcRenderer } = require('electron');

export function App({
  workspaces,
  updateInterval,
  loadDataset,
  loadSensors,
  loadRecommendations,
}) {
  useEffect(() => {
    async function loadDatasets(workspaces) {
      // eslint-disable-next-line array-callback-return
      workspaces.map(async (workspaceId) => {
        // TODO: refactor with api middleware
        await requestCalculation(workspaceId);
        loadDataset(workspaceId);
      });
    }

    async function loadSensorsData(workspaces) {
      // eslint-disable-next-line array-callback-return
      workspaces.map((workspaceId) => {
        loadSensors(workspaceId);
      });
    }

    function onStart() {
      // eslint-disable-next-line array-callback-return
      workspaces.map(async (workspaceId, index) => {
        await requestCalculation(workspaceId);
        await loadDataset(workspaceId);
        await loadSensors(workspaceId);
        if (!index) ipcRenderer.send('show', true);
      });
    }

    onStart();

    const uiUpdateInterval = setInterval(async () => {
      loadDatasets(workspaces);
    }, updateInterval);
    const sensorsUpdateInterval = setInterval(async () => {
      loadSensorsData(workspaces);
    }, updateInterval / 6);
    const recsUpdateInterval = setInterval(async () => {
      loadRecommendations('M100');
    }, updateInterval * 2);

    return () => {
      clearInterval(uiUpdateInterval);
      clearInterval(sensorsUpdateInterval);
      clearInterval(recsUpdateInterval);
    };
  }, []); // eslint-disable-line

  return (
    <div className={styles.Root}>
      <FrameBar />
      <Navigation />
      <Workspace />
    </div>
  );
}

const mapStateToProps = createStructuredSelector({
  workspaces: allWorkspaces,
  updateInterval: updateInterval,
});

const mapDispatchToProps = (dispatch) => ({
  loadDataset: (id) => dispatch(loadDataset(id)),
  loadSensors: (id) => dispatch(loadSensorsData(id)),
  loadRecommendations: (id) => dispatch(requestNewRecs(id)),
});

export default connect(mapStateToProps, mapDispatchToProps)(App);
