import React from 'react';

import styles from './InfoWidgets.module.css';
import InfoWidget from './InfoWidget';
import { connect } from 'react-redux';
import { createStructuredSelector } from 'reselect';
import { allWorkspaces } from '../../../redux/appProperties/selectors';

const InfoWidgets = ({ workspaces }) => {
  return (
    <div className={styles.Root}>
      {workspaces.map((item) => (
        <InfoWidget key={item} workspaceId={item} />
      ))}
    </div>
  );
};

const mapStateToProps = createStructuredSelector({
  workspaces: allWorkspaces,
});

export default connect(mapStateToProps)(InfoWidgets);
