import {
  CLOSE_SPEC_DIALOG,
  OPEN_SPEC_DIALOG,
  SAVE_SPECIFICATIONS,
  SET_SPECIFICATIONS,
  SET_REGIME,
  UPDATE_SPECIFICATIONS,
} from '../constants';
import { saveSpecificationsRequestBuilder } from '../utils';

export const openSpecDialog = (id) => async (dispatch) => {
  dispatch({
    type: OPEN_SPEC_DIALOG,
    payload: { id },
    CallAPI: `${id}/current_specification_and_consts`,
  });
};

export const updateSpecInputs = (id, regime) => async (dispatch) => {
  dispatch({
    type: UPDATE_SPECIFICATIONS,
    payload: { id },
    CallAPI: `${id}/current_consts?specification=${regime}`,
  });
};

export const closeSpecDialog = () => ({
  type: CLOSE_SPEC_DIALOG,
  payload: {},
});

export const setSpecifications = (specs) => ({
  type: SET_SPECIFICATIONS,
  payload: { specs },
});

export const setRegime = (regime) => ({
  type: SET_REGIME,
  payload: { regime },
});

export const saveSpecifications = () => async (dispatch, getState) => {
  const state = getState();
  const { specs, workspaceId } = state.specDialog;
  const request = saveSpecificationsRequestBuilder(
    `${workspaceId}/set_scepification_and_consts`,
    specs[workspaceId].entities,
    workspaceId
  );

  dispatch({
    type: SAVE_SPECIFICATIONS,
    payload: { request },
    CallAPI: request,
  });
};
