import React, { useRef } from 'react';
import cn from 'classnames';

import { useDetectOutsideClick } from '../../../../../../hooks/useOutsideClick';

import ArrowImg from '../../../../../../assets/images/Plot_Streams_Arrow.png';

import styles from './Streams.module.css';
import Stream from './Stream';
import { createStructuredSelector } from 'reselect';
import { plotsStreamsByWorkspaceId } from '../../../../../../redux/plotsStreams/selectors';
import { connect } from 'react-redux';
import { plotsPropsByWorkspaceId } from '../../../../../../redux/plotsProperties/selectors';

const Streams = ({ plotId, plotProps, workspaceId, streams }) => {
  const dropdownRef = useRef(null);
  const [isActive, setIsActive] = useDetectOutsideClick(dropdownRef, false);
  const onClick = () => setIsActive(!isActive);

  const currentPlotStream = plotProps[plotId].streamId;

  return (
    <div className={styles.Root}>
      <button onClick={onClick} className={styles.MenuTrigger}>
        <div className={styles.MenuTriggerText}>
          {streams[currentPlotStream].text}
        </div>
        <div
          className={cn(styles.MenuTriggerIcon, {
            [styles.LessIcon]: isActive,
          })}
        >
          <img alt="" src={ArrowImg} />
        </div>
      </button>
      <ul
        ref={dropdownRef}
        className={cn(styles.Menu, { [styles.active]: isActive })}
      >
        {Object.keys(streams).map((streamId) => (
          <Stream
            key={streamId}
            text={streams[streamId].text}
            streamId={streamId}
            plotId={plotId}
            workspaceId={workspaceId}
            closeDropdown={onClick}
          />
        ))}
      </ul>
    </div>
  );
};

const mapStateToProps = createStructuredSelector({
  plotProps: plotsPropsByWorkspaceId,
  streams: plotsStreamsByWorkspaceId,
});

export default connect(mapStateToProps)(Streams);
