import {
  SET_PLOT_STREAM,
  SET_PLOT_PERIOD,
  SET_PLOT_TARGETS,
} from '../constants';
import { streamDefaultTargetsSelector } from '../plotsStreams/selectors';

export const setPlotStream = (plotId, streamId, workspaceId) => async (
  dispatch,
  getState
) => {
  const state = getState();
  const defaultTargets = streamDefaultTargetsSelector(state, {
    workspaceId,
    streamId,
  });

  dispatch({
    type: SET_PLOT_STREAM,
    payload: { plotId, streamId, defaultTargets, workspaceId },
  });
};

export const setPlotTargets = (plotId, targets, workspaceId) => ({
  type: SET_PLOT_TARGETS,
  payload: { plotId, targets, workspaceId },
});

export const setPlotPeriod = (plotId, period, workspaceId) => ({
  type: SET_PLOT_PERIOD,
  payload: { plotId, period, workspaceId },
});
