import {
  CLOSE_REC,
  REQUEST_NEW_RECS,
  UPDATE_REC_QUEUE,
  UPDATE_SHOWN_RECS,
} from '../constants';
import { recSubmitActionRequestBuilder } from '../utils';

export const requestNewRecs = (id) => async (dispatch, getState) => {
  dispatch({
    type: REQUEST_NEW_RECS,
    payload: { id },
    CallAPI: `${id}/recommendations`,
  });
};

export const updateShownRecs = (newRecs) => ({
  type: UPDATE_SHOWN_RECS,
  payload: { newRecs },
});

export const updateQueue = (newRec) => ({
  type: UPDATE_REC_QUEUE,
  payload: { newRec },
});

export const hideRec = (id) => ({
  type: CLOSE_REC,
  payload: { id },
});

export const recActionSubmit = (action, id) => async (dispatch, getState) => {
  const store = getState();
  const recProps = store.recommendations.shown.filter((rec) => rec.id === id);
  const request = recSubmitActionRequestBuilder(`rec_api`, recProps, action);

  dispatch({
    type: CLOSE_REC,
    payload: { id },
  });
};
