import React from 'react';

import Advice from './Advice';

import styles from './Advices.module.css';
import './Advices.css';
import { createStructuredSelector } from 'reselect';
import { currentWorkspace } from '../../../redux/appProperties/selectors';
import { connect, useSelector } from 'react-redux';
import { CSSTransition, SwitchTransition } from 'react-transition-group';
import { advicesSelector } from '../../../redux/advices/selectors';

const Advices = ({ workspaceId, advices }) => {
  let type = 'full';
  let advicesByWorkspaceId = advices[workspaceId][type];
  const data = useSelector(
    (state) => state.datasets[workspaceId].entities['actual_scheme']
  );

  if (workspaceId === 'M100' && data) {
    const scheme_marker = Number(Object.values(data).slice(-1)[0]);

    if (scheme_marker === 3) {
      type = 'full';
      advicesByWorkspaceId = advices[workspaceId][type];
    } else {
      type = 'mini';
      advicesByWorkspaceId = advices[workspaceId][type];
    }
  }

  return (
    <SwitchTransition>
      <CSSTransition
        key={workspaceId}
        addEndListener={(node, done) =>
          node.addEventListener('transitionend', done, false)
        }
        classNames="advices-fade"
      >
        <div className={styles.Root}>
          {Object.keys(advicesByWorkspaceId).map((key) => (
            <Advice
              key={`${key}`}
              name={key}
              optTargetId={advicesByWorkspaceId[key].optTarget}
              realTargetId={advicesByWorkspaceId[key].realTarget}
              margin={advicesByWorkspaceId[key].margin}
              units={advicesByWorkspaceId[key].units}
              precision={advicesByWorkspaceId[key].precision}
              workspaceId={workspaceId}
            />
          ))}
        </div>
      </CSSTransition>
    </SwitchTransition>
  );
};

const mapStateToProps = createStructuredSelector({
  workspaceId: currentWorkspace,
  advices: advicesSelector,
});

export default connect(mapStateToProps)(Advices);
